.modal {
  position: fixed;
  z-index: 1;

  display: flex;
  align-items: center;
  justify-content: center;

  top: 0;
  left: 0;

  width: 100%;
  min-height: 100vh;

  background: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.content {
  box-sizing: border-box;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 36px 42px 44px;

  width: 444px;

  background: #ffffff;
  border-radius: 12px;
}

.title {
  margin: 0;
  margin-bottom: 30px;

  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
}

.text {
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.05px;
}

.fields {
  display: flex;
  flex-direction: column;
  gap: 30px;

  margin-bottom: 40px;
}

.input {
  width: 100%;

  padding: 12px 1px 6px;

  font-family: inherit;
  font-size: 18px;
  font-weight: 400;
  line-height: 24px;
  letter-spacing: -0.05px;

  border: none;
  outline: none;
  border-bottom: 1px solid #d0cece;
}

.input::placeholder {
  color: #d0cece;
}

.input:focus {
  border-bottom: 1px solid #580ea2;
}
